___CSS_LOADER_EXPORT___.push([module.id, `body {
    font-family: Roboto, Arial, Helvetica, sans-serif;    
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: #16524d;
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #c03636;
    color: white;
    padding: 10px 100px;
}

.favicon {
    display: flex;
    align-items: center;
    gap: 20px;
}

.favicon img {
    width: 60px;
    height: auto;
}

.favicon h3 {
    width: 50%;
}

ul {
    display: flex;
    justify-content: center;
    gap: 50px;
    font-weight: bold;
}

li {
    list-style-type: none;
}

li:hover {
    cursor: pointer;
    color: black;
    text-decoration: underline;
}

.banner-img {
    width: 600px;
    height: auto;
}

#content {
    display: flex;
    justify-content: center;
    gap: 30px;
}

.left {
    padding-left: 100px;
}

.left h1 {
    width: 70%;
    font-size: 54px;
}

.left p {
    width: 90%;
}

.order {
    background-color: #16524d;
    color: white;
    font-weight: bold;
    font-size: 28px;
    padding: 10px;
    border: none;
}

.order:hover {
    cursor: pointer;
}

#heading {
    font-size: 54px;
    text-align: center;
}

.menu-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    justify-content: center;
    align-items: center;
}

.item {
    background-color: #16524d;
    padding: 10px;
    color: white;
}

.item img {
    width: 400px;
    height: 300px;
}

.contact-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.contact-item {
    border: 5px solid #16524d;
    border-radius: 5px;
    width: 400px;
    padding: 10px;
    color: #16524d;
}

footer {
    margin-top: 70px;
    width: 100%;
    background-color: #c03636;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
}

@media (max-width: 1050px) {

    nav {
        justify-content: space-around;
    }

    #content {
        flex-direction: column-reverse;
        align-items: center;
    }

    .menu-container {
        grid-template-columns: repeat(1, 1fr);
    }
}

@media (max-width: 600px) {

    .banner-img {
        width: 400px;
        height: auto;
    }

    .left {
        display: flex;
        flex-direction: column;
        padding-left: 40px;
        align-items: flex-start;
    }

    .left h1 {
        font-size: 48px;
    }

    .order {
        font-size: 24px;
    }

    .menu-container {
        grid-template-columns: repeat(1, 1fr);
    }

    .favicon {
        display: none;
    }

    nav {
        /* justify-content: center; */
        /* align-items: center; */
        padding: 10px 0px;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAEA;IACI,iDAAiD;IACjD,WAAW;IACX,YAAY;IACZ,sBAAsB;IACtB,iBAAiB;IACjB,aAAa;IACb,sBAAsB;IACtB,8BAA8B;IAC9B,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,yBAAyB;IACzB,YAAY;IACZ,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,eAAe;IACf,YAAY;IACZ,0BAA0B;AAC9B;;AAEA;IACI,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,SAAS;AACb;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,UAAU;IACV,eAAe;AACnB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,iBAAiB;IACjB,eAAe;IACf,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,qCAAqC;IACrC,SAAS;IACT,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,yBAAyB;IACzB,kBAAkB;IAClB,YAAY;IACZ,aAAa;IACb,cAAc;AAClB;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,yBAAyB;IACzB,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;;IAEI;QACI,6BAA6B;IACjC;;IAEA;QACI,8BAA8B;QAC9B,mBAAmB;IACvB;;IAEA;QACI,qCAAqC;IACzC;AACJ;;AAEA;;IAEI;QACI,YAAY;QACZ,YAAY;IAChB;;IAEA;QACI,aAAa;QACb,sBAAsB;QACtB,kBAAkB;QAClB,uBAAuB;IAC3B;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,qCAAqC;IACzC;;IAEA;QACI,aAAa;IACjB;;IAEA;QACI,6BAA6B;QAC7B,yBAAyB;QACzB,iBAAiB;IACrB;AACJ","sourcesContent":["@import url(https://fonts.googleapis.com/css?family=Roboto:100,100italic,300,300italic,regular,italic,500,500italic,700,700italic,900,900italic);\r\n\r\nbody {\r\n    font-family: Roboto, Arial, Helvetica, sans-serif;    \r\n    margin: 0px;\r\n    padding: 0px;\r\n    box-sizing: border-box;\r\n    min-height: 100vh;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n    color: #16524d;\r\n}\r\n\r\nnav {\r\n    display: flex;\r\n  